<template>
	<q-layout view="hHh lpR fFf">
		<q-header>
			<q-toolbar>
				<q-toolbar-title>
					<icon name="mdi-cog" vAlign="middle" />
					{{ $t('Configuration') }}
				</q-toolbar-title>
				
				<q-btn flat dense round :icon="expertMode ? 'mdi-code-tags-check' : 'mdi-code-tags'" aria-label="$t('Expert Mode')" @click="expertMode = !expertMode">
					<q-tooltip>{{ $t('Expert Mode') }}</q-tooltip>
				</q-btn>
			</q-toolbar>
			
			<q-tabs v-model="configTab" inline-label dense outside-arrows mobile-arrows align="left">
				<q-route-tab :to="tab.id" :label="$t(tab.title)" :icon="tab.icon" v-for="tab in tabs" :key="tab.id" :name="tab.id" />
			</q-tabs>
		</q-header>
		
		<q-page-container class="ConfigurationPage body">
			<q-page :style-fn="pageContainer">
				<router-view />
			</q-page>
		</q-page-container>
		
		<q-footer>
			<q-toolbar>
				<q-btn flat icon="mdi-window-close" aria-label="Cancel" label="Cancel" class="text-red" @click="cancel" />
				<q-space />
				<q-btn :disable="hasErrors" icon="mdi-content-save" aria-label="Save" label="Save" flat color="primary" @click="save(false)" style="margin-right: 8px" />
				<q-btn :disable="hasErrors" icon="mdi-content-save" aria-label="Save & Exit" label="Save & Exit" color="primary" @click="save(true)" />
			</q-toolbar>
		</q-footer>

	</q-layout>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

import Devices from 'src/controllers/Devices';

const TABS = [{
		'id': 'devices',
		'title': 'Devices',
		'icon': 'mdi-devices'
	}, {
		'id': 'widgets',
		'title': 'Widgets',
		'icon': 'mdi-view-grid'
	}, {
		'id': 'layout',
		'title': 'Layout',
		'icon': 'mdi-file'
	}, {
		'id': 'styles',
		'title': 'Styles',
		'icon': 'mdi-code-tags',
		'pro': true
	}, {
		'id': 'settings',
		'title': 'Settings',
		'icon': 'mdi-cog'
}];

import i18n from './i18n'
export default defineComponent({
	'name': 'ConfigurationPage',
	
	data() {
		return {
			'tabs': TABS
		}
	},
	
	'methods': {
		pageContainer(offset, height) {
			this.$store.commit('jarvis/set', { 'pageHeight': (height - offset) });
			return { 'min-height': (height - offset) + 'px' } // document.body.scrollHeight > height ? { 'min-height': (height-offset) + 'px' } : { 'height': (height-offset) + 'px' }
		},
		cancel() {
			this.$store.commit('jarvis/set', { 'drawerLeftOpen': false });
			this.$router.push({ 'name': 'home', 'params': { 'tabId': this.$store.getters['jarvis/getSelectedTabId'] } }).catch(() => {});
			
			setTimeout(() => {
				this.$store.commit('editor/reset');
			}, 1000);
		},
		save(exit) {
			const devices = this.$store.getters['editor/getDevicesWithoutDeletions'];
			const layout = this.$store.getters['editor/getLayoutWithoutDeletions'];
			const widgets = this.$store.getters['editor/getWidgetsWithoutDeletions'];
			const styles = this.$store.state.editor.styles;
			const settings = this.$store.state.editor.settings;
			
			// save to store
			this.$store.commit('jarvis/set', { devices, layout, widgets, settings, styles });
			this.$store.commit('editor/init', { devices, layout, widgets, settings, styles });
			Devices._devices = {};
			
			// save to backend
			this.$store.dispatch('editor/save');
			
			// reset editor
			if (exit) {
				this.cancel();
			}
		}
	},
	
	'computed': {
		hasErrors() {
			return this.$store.getters['editor/hasErrors'];
		},
		'expertMode': {
			get() {
				return this.$store.state.editor.settings?.configExpertMode;
			},
			set(val) {
				this.$store.commit('editor/set', { 'settings.configExpertMode': val });
			}
		},
		'configTab': {
			get() {
				return this.$store.state.editor.settings?.configTab;
			},
			set(val) {
				if (val) {
					this.$store.commit('editor/set', { 'settings.configTab': val });
				}
			}
		}
	},
	
	beforeCreate() {
		i18n.register(this.$i18n, i18n.translations);
		
		// load latest config into editor
		this.$store.commit('editor/init', {
			'devices': this.$store.state.jarvis.devices,
			'layout': this.$store.state.jarvis.layout,
			'styles': this.$store.state.jarvis.styles,
			'settings': this.$store.state.jarvis.settings,
			'widgets': this.$store.state.jarvis.widgets
		});
		
		// select initial tab
		this.$router.push({ 'path': '/configuration/' + (this.$store.state.editor.settings?.configTab || TABS[0].id) }).catch(() => {});
	}
});
</script>
